  border-radius: 5px;
  height: 45px;
}

input:focus {
  outline: none !important;
  border: 1px solid #c27c7c;
}

input[type="submit"] {
  height: 50px;
  font-weight: 600;
  font-size: 17px;
  color: #ffffff;
  background: #233f76;
