  border-radius: 100px;
  transition: all 0.35s;
}

input[type="submit"]:hover {
  background: #2d373c;
}

input[type="submit"]:focus {
  outline: none;
}

textarea {
  padding: 8px 10px;
  background: #ffffff;
  border-radius: 5px;
  display: block;
  width: 100%;
  border: 1px solid #eee;
  transition: all 0.4s ease;
}

select {
  padding: 8px 10px;
  background: #ffffff;
  border: 1px solid #ffffff;
  border-radius: 5px;
  display: block;
  width: 100%;
  transition: all 0.4s ease;
  margin-bottom: 10px;
  height: 45px;
}

select:focus {
  outline: none;
  border: 1px solid #ff5235;
}

/* *****
table's defult style
