  height: 35px;
  border-radius: 50%;
  border: 1px solid #233f76;
  box-sizing: border-box;
  font-weight: 800;
  font-size: 15px;
  text-transform: uppercase;
  color: #2d373c;
  text-align: center;
  line-height: 34px;
  transition: all 0.3s;
}

.custom-pagination .page-list li a:hover {
